o-image_2.jpg\");\n\tbackground-position: center;\n\tposition: relative;\n}\n.hero::before {\n\tcontent: \"\";\n\tbackground: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.8));\n\tbackground-position: right;\n\tbackground-repeat: no-repeat;\n\topacity: 0.9;\n\tposition: absolute;\n\ttop: 0;\n\tleft: 0;\n\tbottom: 0;\n\tright: 0;\n}\n\n.hero-inner {\n\tmargin: 0 auto;\n\tmax-width: 800px;\n\tz-index: 1;\n}\n\n.hero-title {\n\tcolor: #fff;\n\tfont-weight: 500;\n\tfont-size: 36px;\n\topacity: 1;\n}\n\n.hero-tagline {\n\tcolor: #fff;\n\tmargin-top: 16px;\n\tfont-size: 18px;\n\tfont-weight: 300;\n}\n\nmain {\n\twidth: 100%;\n}\n\n.main-content {\n\twidth: 100%;\n\tpadding: 32px;\n}\n\n.main-content-container {\n\twidth: 100%;\n\tmargin: 10px 0px;\n\ttext-align: center;\n\tdisplay: flex;\n\tflex-direction: column;\n\tjustify-items: center;\n}\n\n.breaker {\n\theight: 100px;\n\tmargin-top: 1vmin;\n\tposition: relative;\n\tmargin-bottom: 20px;\n}\n\n.breaker .text {\n\tborder-top: 2px solid black;\n\tborder-bottom: 2px solid black;\n\twidth: 100%;\n\tdisplay: block;\n\tmargin: auto;\n\tpadding: 10px 25px;\n\tfont-size: 1.8em;\n\tfont-weight: 600;\n\tcolor: var(--third);\n\ttext-shadow: 4px 4px 7px rgb(197, 196, 196);\n\t/* white-space: wrap; */\n\tline-height: 1.2em;\n\ttext-transform: uppercase;\n\tletter-spacing: 5px;\n}\n\n.skip-link {\n\tposition: absolute;\n\ttop: -40px;\n\tleft: 0;\n\tpadding: 8px;\n\tbackground-color: var(--primary);\n\tcolor: white;\n\tz-index: 100;\n}\n.skip-link:focus {\n\ttop: 0;\n}\n\n.card {\n\twidth: 100%;\n\ttext-align: left;\n\tdisplay: grid;\n}\n\n.card-items {\n\tmargin: 16px 0px;\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n\twidth: 300px;\n\tborder-radius: 10px;\n\toverflow: hidden;\n}\n\n.card-items-content {\n\tpadding: 16px 32px 32px 32px;\n}\n\n.card-items-header {\n\twidth: 100%;\n\taspect-ratio: 3/2;\n\tobject-fit: cover;\n}\n.card-text-city {\n\tfont-size: 15px;\n\ttext-transform: capitalize;\n\tcolor: var(--third);\n\tdisplay: flex;\n\tjustify-content: space-between;\n\talign-items: baseline;\n\tfont-weight: 700;\n}\n\n.card-text-rating {\n\tcolor: var(--primary);\n\ttext-decoration: none;\n\tfont-size: 14px;\n}\n\n.card-text-title {\n\tfont-weight: 900;\n\tfont-size: 18px;\n\tmargin-top: 16px;\n\tcolor: var(--primary);\n}\n\n.card-text-description {\n\tmargin-top: 16px;\n\tfont-size: 14px;\n\tline-height: 1.5em;\n\theight: 200px;\n\toverflow: hidden;\n\ttext-overflow: clip;\n\tposition: relative;\n\ttext-align: justify;\n}\n.card-text-description::before {\n\tcontent: \"\";\n\tposition: absolute;\n\tbottom: 0;\n\tleft: 0;\n\theight: 4%;\n\twidth: 100vw;\n\tbackground: linear-gradient(to bottom, transparent, white);\n\tpointer-events: none;\n}\n\n.card-text-description::-webkit-scrollbar {\n\twidth: 7px;\n\tborder-radius: 50px;\n}\n\n.card-text-description::-webkit-scrollbar-track {\n\tbackground: #f1f1f1;\n}\n\n.card-text-description::-webkit-scrollbar-thumb {\n\tbackground: var(--secondary);\n\tborder-radius: 50px;\n}\n\n.card-text-description::-webkit-scrollbar-thumb:hover {\n\tbackground: var(--primary);\n}\n\n.card-items-button {\n\tmargin-top: 15px;\n\tfont-size: 15px;\n\twidth: 140px;\n\tletter-spacing: 1px;\n\theight: 44px;\n\tborder: none;\n\tbackground-color: var(--primary);\n\tcolor: var(--light);\n\tborder-radius: 25px;\n}\n\n.card-items .custom-item {\n\toverflow-y: visible;\n\theight: 100%;\n}\n.card-items .custom-item::after {\n\tbackground: transparent;\n}\n\nfooter {\n\tpadding-top: 20px;\n}\n.footer-container {\n\tdisplay: flex;\n\tflex-direction: row;\n\talign-items: baseline;\n\tjustify-content: flex-start;\n\twidth: 100%;\n\tpadding: 40px 150px;\n\tgap: 150px;\n\tbackground-color: #0f172a;\n}\n\n.group-article h2::before,\n.link-section h2::before {\n\tcontent: \" \";\n\twhite-space: pre;\n\tborder-left: 5px solid;\n\tborder-color: var(--primary);\n}\n\n.person-info p {\n\tline-height: 1.6em;\n\tpadding-left: 10px;\n\ttext-align: justify;\n\tfont-size: 14px;\n}\n\n.person-info h2 {\n\tfont-weight: bold;\n\twhite-space: nowrap;\n\tfont-size: 1.7em;\n\tmargin-bottom: 10px;\n\tcolor: var(--primary);\n\tpadding-left: 10px;\n}\n.person-info h3:hover {\n\tcolor: var(--primary);\n\ttransition: ease-in-out 300ms;\n}\n.group-article,\n.link-section,\n.person-info {\n\tdisplay: grid;\n\tflex-wrap: wrap;\n\tcolor: var(--secondary);\n}\n\n.group-article h2,\n.link-section h2,\n.person-info h3 {\n\tfont-weight: bold;\n\tfont-size: 1.2em;\n\tmargin-bottom: 10px;\n\tcolor: var(--light);\n\tpadding-left: 10px;\n\twhite-space: nowrap;\n}\n.group-article ul,\n.group-article li,\n.group-article span {\n\ttext-decoration: none;\n\tlist-style-type: none;\n\tcolor: var(--secondary);\n\tpadding: 13px 7px;\n\n\tfont-size: 15px;\n}\n\n.link-section ul,\n.link-section li,\n.link-section a {\n\ttext-decoration: none;\n\tlist-style-type: none;\n\tcolor: var(--secondary);\n\tpadding: 13px 7px;\n\tfont-size: 15px;\n\twhite-space: nowrap;\n}\n\n.link-section ul li a:hover {\n\tcolor: var(--light);\n\ttransition: ease-in-out 300ms;\n}\n\n.wrapper {\n\twidth: 100%;\n\tbackground-color: var(--third);\n}\n.logo-footer {\n\twidth: 80%;\n\tdisplay: flex;\n\tborder-top: 1px solid;\n\tborder-color: var(--secondary);\n\tflex-direction: column;\n\talign-items: center;\n\tpadding-bottom: 20px;\n\tmargin: auto;\n\tgap: 10px;\n}\n.logo-footer img {\n\twidth: 300px;\n}\n.logo-footer p {\n\twidth: 100%;\n\tcolor: var(--light);\n\ttext-align: center;\n}\n.logo-footer span {\n\tcolor: var(--primary);\n\ttext-align: center;\n}\n\n/* example */\n.container-upper {\n}\n.container-detail {\n\t/* width: 100%; */\n\t/* height: 500px; */\n\t/* background: rgb(128, 68, 68); */\n\tdisplay: grid;\n\t/* grid-template-columns: repeat(2, 1fr); */\n\tgrid-template-columns: repeat(auto-fit, minmax(0.5fr, 1fr));\n\ttext-align: left;\n\trow-gap: 13px;\n}\n\n.wrapping-detail {\n\tdisplay: grid;\n\tgrid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n\n\t/* grid-column: 1 / span 2; */\n}\n.image-detail img {\n\theight: 100%;\n\twidth: 100%;\n\tobject-fit: cover;\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n}\n.text-detail {\n\t/* background: rgb(117, 55, 55); */\n\tpadding: 40px;\n\tline-height: 27px;\n\ttext-align: left;\n\tbackground: var(--detail);\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n\tfont-size: 14px;\n\tcolor: #1749c7;\n}\n.text-detail p {\n\tcolor: #5677a7;\n}\n.menus {\n\tdisplay: grid;\n\tgrid-template-columns: repeat(auto-fit, minmax(390px, 1fr));\n\tmargin-top: 10px;\n\tgap: 10px;\n\tjustify-content: space-evenly;\n}\n.menu-drinks,\n.menu-foods {\n\tpadding: 40px;\n\twidth: 100%;\n\ttext-align: left;\n\tbackground: var(--detail);\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n\tcolor: #1749c7;\n\tfont-size: 14px;\n}\n\n.menu-drinks ul li,\n.menu-foods ul li {\n\t/* width: 100%; */\n\ttext-align: left;\n\tline-height: 31px;\n\tmargin: 2px 28px;\n\tcolor: #5677a7;\n\tfont-size: 15px;\n}\n.user-review {\n\tmargin: 10px 0px;\n\tcolor: #1749c7;\n\ttext-align: center;\n}\n.reviews {\n\twidth: 100%;\n\tdisplay: grid;\n\t/* grid-template-columns: repeat(3, 1fr); */\n\tgrid-template-columns: repeat(auto-fit, minmax(370px, 1fr));\n\tgap: 10px;\n\tmargin-top: 30px;\n\t/* padding: 0px 50px; */\n\tjustify-content: space-around;\n}\n/* .container-card-review {\n\tdisplay: grid;\n\tgap: 30px;\n} */\n\n.card-review {\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n\ttransition: 0.3s;\n\theight: auto;\n\tpadding: 20px;\n\tline-height: 24px;\n\ttext-align: left;\n\tcolor: #5677a7;\n\tbackground: var(--detail);\n}\n\n.card-review:hover {\n\tbox-shadow: 0 8px 16px 0 rgba(5, 16, 116, 0.2);\n\tcolor: #1749c7;\n}\n.wrap-review {\n\tdisplay: flex;\n\tjustify-content: space-between;\n\tpadding-bottom: 10px;\n}\n\n.like {\n\tfont-size: 18px;\n\tposition: fixed;\n\tbottom: 16px;\n\tright: 16px;\n\tbackground-color: #c60404;\n\tcolor: white;\n\tborder: 0;\n\tborder-radius: 50%;\n\twidth: 55px;\n\theight: 55px;\n\tcursor: pointer;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tz-index: 9999;\n}\n"],"sourceRoot":""}]);
